<!-- eslint-disable vue/multi-word-component-names -->
<template>
<div id="building3">
<div id="page-header">
<el-page-header @back="redirectToLogin('/')" content="">
</el-page-header>
</div>
<div id="usermessage">
    <el-descriptions title="" id = "usertitle">
    <el-descriptions-item label="">{{ this.username }}</el-descriptions-item>
</el-descriptions>
<el-button @click="redirectToLogin('message')"></el-button>
</div>
<h2 class="list-title"></h2>
<el-divider id="dj"></el-divider>
  <div class="question-list1">
  <div v-if="notnull1">
    <div v-for="answer in answers" :key="answer.answerid" class="question-item">
      <!--  question -->
      <div class="question-info">
        <p class="question-content">: {{ answer.answer }}</p>
        <p class="question-username">: {{ answer.username }}</p>
         <p class="question-question">: {{ answerQuestionMap[answer.questionid] }}</p>
        <button @click="showanswerDialog(answer.answerid)">
        <i class="el-icon-edit"></i>
        </button>
        <button @click="showalertDialog(answer.answerid)">
        <i class="el-icon-delete"></i>
        </button>
      </div>
      <el-divider id="dj"></el-divider>
    </div>
    </div>
  <div v-else>
    <el-empty description=""></el-empty>
   </div>
</div>
<h2 class="list-title"></h2>
<div class="userquestion-list">
<div v-if="notnull2">
    <div v-for="question in questions" :key="question.questionid" class="question-item">
      <!--  question -->
      <div class="question-info">
        <p class="question-content">: {{ question.question }}</p>
        <p class="question-username">: {{ question.username }}</p>
        <button @click="showquestionDialog(question.questionid)">
        <i class="el-icon-edit"></i>
        </button>
        <button @click="showalertDialog2(question.questionid)">
        <i class="el-icon-delete"></i>
        </button>
      </div>
      <el-divider id="dj"></el-divider>
    </div>
    </div>
    <div v-else>
    <el-empty description=""></el-empty>
   </div>
</div>
<el-dialog
    title=""
    :visible.sync="questionDialogVisible"
    width="30%"
    :before-close="handleClose"
  id ="ques">
    <el-input v-model="question" placeholder=""></el-input>
    <span slot="footer" class="dialog-footer">
      <el-button @click="questionDialogVisible = false"></el-button>
      <el-button type="primary" @click="modifyq"></el-button>
    </span>
  </el-dialog>
  <el-dialog
    title=""
    :visible.sync="alertdialogvisible2"
    width="30%"
    :before-close="handleClose"
  id ="ques">
    <span>?</span>
    <span slot="footer" class="dialog-footer">
      <el-button @click="alertdialogvisible2 = false"></el-button>
      <el-button type="primary" @click="deleteq"></el-button>
    </span>
  </el-dialog>
  <el-dialog
    title=""
    :visible.sync="answerdialogvisible"
    width="30%"
    :before-close="handleClose"
  id ="ques">
    <el-input v-model="answer" placeholder=""></el-input>
    <span slot="footer" class="dialog-footer">
      <el-button @click="answerdialogvisible = false"></el-button>
      <el-button type="primary" @click="modifya"></el-button>
    </span>
  </el-dialog>
  <el-dialog
    title=""
    :visible.sync="alertdialogvisible"
    width="30%"
    :before-close="handleClose"
  id ="ques">
    <span>?</span>
    <span slot="footer" class="dialog-footer">
      <el-button @click="alertdialogvisible = false"></el-button>
      <el-button type="primary" @click="deletea"></el-button>
    </span>
  </el-dialog>
</div>
</template>
<script>
import { useranswer } from '@/api/answer'
import { userquestion, getquestion } from '@/api/question'
import { user, modifya, deletea, modifyq, deleteq } from '@/api/user'
export default {
  data() {
    return {
      answers: [], // 
      questions: [],
      answer: '',
      question: '',
      questionDialogVisible: false,
      answerdialogvisible: false,
      questionid: '',
      currentquestionid: '',
      username: '',
      currentanswerid: '',
      alertdialogvisible: false,
      alertdialogvisible2: false,
      notnull1: true,
      notnull2: true,
      answerQuestionMap: {}
    }
  },
  created() {
    this.getusername()
    this.Getuserquestion()
    this.Getuseranswer()
  },
  methods: {
   goBack() {
        console.log('go back')
      },
   async redirectToLogin(name) {
      this.$router.push(name)
    },
    showquestionDialog(id) {
      this.questionDialogVisible = true
      this.currentquestionid = id
    },
    showanswerDialog(id) {
      this.answerdialogvisible = true
      this.currentanswerid = id
    },
    showalertDialog2(id) {
     this.alertdialogvisible2 = true
     this.currentquestionid = id
     },
     showalertDialog(id) {
     this.alertdialogvisible = true
     this.currentanswerid = id
     },
    handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done()
          })
          .catch(_ => {})
      },
      async getusername() {
      const { data: res } = await user()
      this.username = res.username
      console.log(this.username)
      },
      async Getuseranswer() {
        const { data: res } = await useranswer()
        if (res.res == null || res.res == 'null') {
           this.notnull1 = false
        } else {
        this.answers = res.res.reverse()
        for (const answer of this.answers) {
        const postData = new URLSearchParams() //  postform 
       postData.append('questionid', answer.questionid)
      const { data: res1 } = await getquestion(postData)
      this.$set(this.answerQuestionMap, answer.questionid, res1.question[0].question)
      console.log(res1.question[0].question)
    }
        }
      },
      async Getuserquestion() {
      const { data: res } = await userquestion()
      if (res.res == null || res.res == 'null') {
           this.notnull2 = false
        } else {
        this.questions = res.res.reverse()
        console.log(this.answer)
        console.log(res)
        }
      },
      async modifya() {
        const postData = new URLSearchParams()//  postform 
        postData.append('answer', this.answer)
        postData.append('answerid', this.currentanswerid)
         const { data: res } = await modifya(postData)
        if (res.status !== '200') {
           this.$message({
           message: res.message,
             type: 'error',
            showClose: true,
             duration: 3000
           })
        } else {
        this.$message({
           message: res.message,
             type: 'success',
            showClose: true,
             duration: 3000
           })
        this.Getuseranswer()
        }
        console.log(res)
      },
      async deletea() {
       const postData = new URLSearchParams()//  postform 
        postData.append('answerid', this.currentanswerid)
         const { data: res } = await deletea(postData)
        if (res.status !== '200') {
           this.$message({
           message: res.message,
             type: 'error',
            showClose: true,
             duration: 3000
           })
        } else {
        this.$message({
           message: res.message,
             type: 'success',
            showClose: true,
             duration: 3000
           })
            this.Getuseranswer()
        }
        console.log(res)
      },
      async deleteq() {
      const postData = new URLSearchParams()//  postform 
        postData.append('questionid', this.currentquestionid)
         const { data: res } = await deleteq(postData)
        if (res.status !== '200') {
           this.$message({
           message: res.message,
             type: 'error',
            showClose: true,
             duration: 3000
           })
        } else {
        this.$message({
           message: res.message,
             type: 'success',
            showClose: true,
             duration: 3000
           })
            this.Getuserquestion()
        }
        console.log(res)
      },
      async modifyq() {
        const postData = new URLSearchParams()//  postform 
        postData.append('question', this.question)
        postData.append('questionid', this.currentquestionid)
         const { data: res } = await modifyq(postData)
        if (res.status !== '200') {
           this.$message({
           message: res.message,
             type: 'error',
            showClose: true,
             duration: 3000
           })
        } else {
        this.$message({
           message: res.message,
             type: 'success',
            showClose: true,
             duration: 3000
           })
        this.Getuserquestion()
        }
        console.log(res)
      }
}
}
</script>
<style>
#usermessage{
   background-color: #FFF0F5;
   display: flex;
justify-content: centor;
   margin:0 auto;
   opacity: 0.8;
}
.question-list1{
  height: 250px; /*  */
  overflow-y: auto;
  background-color: #F5F5F5;
  opacity: 0.8;
}
.userquestion-list{
  height: 250px; /*  */
  overflow-y: auto;
  background-color: #F5F5F5;
  opacity: 0.8;
}
#building3 {
  background: url("../../background-uni.png");
  width: 100%;
  height: 100%; /* 80% */
  position: fixed;
  background-size: cover; /* cover */
  background-position: center bottom; /*  */
}
</style>
