<!-- eslint-disable vue/multi-word-component-names -->
<template>
<div id="building2">
<div id="back">
<el-page-header @back="redirectToLogin('/')" content="" class="custom-page-header">
</el-page-header>
</div>
<div>
 <div class="navigator">
 <h1 class="title"> | Ferdinand</h1>
 </div>
<div class="outer-container1">
  <div class="question-list3">
  <div v-if="notnull">
    <div v-for="answer in answers" :key="answer.answerid" class="question-item">
      <!--  question -->
      <div class="question-info">
        <p class="question-content">: {{ answer.answer }}</p>
        <p class="question-username">
          {{ answer.pid == '0' ? ': ' + answer.username : ':' + answer.username + ' ' + answermap[answer.pid] }}
        </p>
        <button @click="showanswerdialog(answer.answerid)" class="answer-button">
        <i class="el-icon-chat-dot-square"></i>
        </button>
      </div>
      <el-divider id="dj"></el-divider>
    </div>
    </div>
    <div v-else>
    <el-empty description=""></el-empty>
   </div>
  </div>
</div>
</div>
<el-dialog
    title=""
    :visible.sync="answerdialogvisible"
    width="30%"
    :before-close="handleClose"
  id ="ques">
    <el-input v-model="answer" placeholder=""></el-input>
    <span slot="footer" class="dialog-footer">
      <el-button @click="answerdialogvisible = false"></el-button>
      <el-button type="primary" @click="Answer"></el-button>
    </span>
  </el-dialog>
</div>
</template>
<script>
import { getanswer, answer } from '@/api/answer'
export default {
  data() {
    return {
      answers: [], // 
      answer: '',
      questionDialogVisible: false,
      answerdialogvisible: false,
      questionid: '',
      currentanswerid: '',
      nowquestionid: '',
      notnull: true,
      answermap: {}
    }
  },
  created() {
    this.Getanswer()
  },
  methods: {
   goBack() {
        console.log('go back')
      },
   async redirectToLogin(name) {
      this.$router.push(name)
    },
    showQuestionDialog() {
      this.questionDialogVisible = true
    },
    showanswerdialog(n) {
    this.answerdialogvisible = true
    this.currentanswerid = n
    },
    handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done()
          })
          .catch(_ => {})
      },
      async Getanswer() {
       const questionId = this.$route.params.questionid
       this.nowquestionid = questionId
       console.log(questionId)
       const postData = new URLSearchParams()
       postData.append('questionid', questionId)
        const { data: res } = await getanswer(postData)
        if (res.res == null || res.res == 'null') {
           this.notnull = false
        } else {
        this.answers = res.res.reverse()
        for (const answer of this.answers) {
        this.$set(this.answermap, answer.answerid, answer.username)
        }
        console.log(res)
        }
       },
      async Answer() {
       const postData = new URLSearchParams() //  postform 
       postData.append('questionid', this.nowquestionid)
       postData.append('answer', this.answer)
       postData.append('pid', this.currentanswerid)
        const { data: res } = await answer(postData)
        if (res.status !== '200') {
           this.$message({
           message: res.message,
             type: 'error',
            showClose: true,
             duration: 3000
           })
        } else {
        this.$message({
           message: res.message,
             type: 'success',
            showClose: true,
             duration: 3000
           })
           this.Getanswer()
        }
      }
}
}
</script>
<style>
#building2 {
  background: url("../../background-head.jpg");
  width: 100%;
  height: 100%; /* 80% */
  position: fixed;
  background-size: cover; /* cover,, */
  background-position: center bottom; /*  */
}
.question-list3 {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #F5F5F5;
  opacity: 0.7;
}
.back {
  color: white;
}
.outer-container1{
  height: 600px; /*  */
  overflow-y: auto;
}
</style>
