import axios from 'axios'
const request = axios.create({
  // baseURL: 'http://127.0.0.1:8088',
   baseURL: '/api',
  //   baseURL: 'http://ten.ferdinandaedth.top:100',
  // `transformResponse`  then/catch 
  transformResponse: [function (data) {
    //  data 
    return data
  }]

})
// 
request.interceptors.request.use(function (config) {
  // 
  config.headers.Authorization = sessionStorage.getItem('token')
  config.headers.Authorization = `Bearer ${config.headers.Authorization}`
  config.headers['Content-Type'] = 'multipart/form-data; boundary=<calculated when request is sent>application/x-www-form-urlencoded'
  return config
}, function (error) {
  // 
  return Promise.reject(error)
})

// 
request.interceptors.response.use(function (response) {
  // 
  const data = response.data
  const responseData = JSON.parse(data)
  return {
    data: responseData
  }
}, function (error) {
  // 
  return Promise.reject(error)
})
export default request
